.input-block__wrapper {
  position: relative;
}
.input-block__label {
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
  position: absolute;
  top: 11px;
  left: 0;
  width: 100%;
  padding: 0 16px;
  color: #94A0B1;
  pointer-events: none;
  transition: color 0.2s, font-size 0.2s, line-height 0.2s, top 0.2s;
}
.input-block__input {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  height: 64px;
  width: 100%;
  padding: 29px 16px 11px;
  color: #151515;
  border: none;
  background-color: #F4F4F8;
  border-radius: 5px;
  transition: background-color 0.2s;
}
.input-block__input:focus {
  outline: none;
}
.input-block__input:not(:disabled):hover {
  background-color: #E6E6EF;
}
.input-block__note {
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
  color: #94A0B1;
  margin: 8px 0 0;
  padding: 0 16px;
  display: none;
}
.input-block__icon {
  position: absolute;
  top: 20px;
  right: 16px;
  display: block;
}
.input-block__icon svg {
  width: 24px;
  height: 24px;
  color: #94A0B1;
}
.input-block__icon-hide {
  display: none;
}
.input-block__icon-btn {
  font-size: 0;
  line-height: 0;
  font-weight: 400;
  margin: 0;
  padding: 0;
  height: auto;
  border: none;
  background-color: transparent;
  position: absolute;
  top: 20px;
  right: 16px;
  display: block;
}
.input-block__icon-btn:hover, .input-block__icon-btn:active, .input-block__icon-btn:focus {
  background-color: transparent;
}
.input-block__icon-btn:hover {
  cursor: pointer;
}
.input-block__icon-btn .input-block__icon {
  position: static;
}

.input-block.show-note .input-block__note {
  display: block;
}

.input-block.js-empty .input-block__label {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #151515;
  top: 20px;
}

.input-block__input:disabled {
  color: #D1D8E1;
}
.input-block__input:disabled ~ .input-block__label, .input-block__input:disabled ~ .input-block__note {
  color: #D1D8E1;
}
.input-block__input:disabled ~ .input-block__icon svg {
  color: #D1D8E1;
}
.input-block__input:disabled ~ .input-block__icon-btn {
  pointer-events: none;
}
.input-block__input:disabled ~ .input-block__icon-btn:hover {
  cursor: default;
}
.input-block__input:disabled ~ .input-block__icon-btn svg {
  color: #D1D8E1;
}

.input-block.js-error .input-block__label,
.input-block.js-error .input-block__note {
  color: #F25248;
}

